angular
  .module('app', ['ui.router']);

(function() {

  'use strict';

  registrationUserController.$inject = ["registration", "$location"];
  angular
    .module('app')
    .component('registrationUser', {
      templateUrl: 'app/pages/registration/user/user.html',
      controller: registrationUserController
    });

  /** @ngInject */
  function registrationUserController(registration, $location) {
    var vm = this;

    vm.submit = function() {
      registration.addUserInfo({
        stepName: 'user',
        firstName: vm.firstName,
        lastName: vm.lastName
      });
      $location.path('/registration/address');
    };

    function onLoad() {
      if (registration.checkIsUserDataStepComplete()) {
        var userInfo = registration.userInfo.user;
        vm.firstName = userInfo.firstName;
        vm.lastName = userInfo.lastName;
      }
    }

    onLoad();
  }
}());

(function() {

  'use strict';

  registrationSuccessController.$inject = ["registration", "$location", "$window"];
  angular
    .module('app')
    .component('registrationSuccess', {
      templateUrl: 'app/pages/registration/success/success.html',
      controller: registrationSuccessController
    });

  /** @ngInject */
  function registrationSuccessController(registration, $location, $window) {
    var vm = this;

    vm.clear = function() {
      $window.localStorage.removeItem('app');
      $location.path('/registration/user');
    };

    function onLoad() {
        vm.userInfo = registration.userInfo;
    }

    onLoad();
  }
}());

(function() {

  'use strict';

  appRegistrationNavController.$inject = ["$location", "$rootScope", "$scope"];
  angular
    .module('app')
    .component('appRegistrationNav', {
      templateUrl: 'app/pages/registration/registration-nav/registration-nav.html',
      controller: appRegistrationNavController,
      bindings: {
        steps: '='
      }
    });

  /** @ngInject */
  function appRegistrationNavController($location, $rootScope, $scope) {
    var vm = this;

    var steps = ['user', 'address', 'card'];
    vm.breadcrumps = [];
    var path,
      currentStep;

    function refreshStep() {
      path = $location.path().split('/');
      path = path[path.length - 1];

      currentStep = steps.indexOf(path);
      vm.breadcrumps = steps.filter(function(el, index) {
        return index <= currentStep;
      });
      vm.previousStep = steps[currentStep - 1] === -1 ? false : steps[currentStep - 1];
    };

    $rootScope.$on('$locationChangeStart', function(e, newUrl) {
      refreshStep();
    });

    vm.goToPreviousStep = function() {
      $location.path('/registration/' + vm.previousStep);
    };

    vm.goToHome = function() {
      $location.path('/registration/' + steps[0]);
    };

    vm.goToStep = function(step) {
      $location.path('/registration/' + step);
    };

    function onInit() {
      refreshStep();
    };

    onInit();
  }

}());

