(function() {

  'use strict';

  registrationFun.$inject = ["$window"];
  angular
    .module('app')
    .factory('registration', registrationFun);

  /** @ngInject */
  function registrationFun($window) {
    var userInfo = angular.fromJson($window.localStorage.getItem('app')) || {};
    var isUserWriteData = !!$window.localStorage.getItem('app');
    var currentStep;

    function checkIsRegComplete() {
      if (userInfo.address && userInfo.card && userInfo.user) {
        return true;
      } else {
        return false;
      }
    };

    function checkIsUserDataStepComplete() {
      if (userInfo.user) {
        return true;
      } else {
        return false;
      }
    };

    function checkIsAddressStepComplete() {
      if (userInfo.address) {
        return true;
      } else {
        return false;
      }
    };

    function checkIsCardStepComplete() {
      if (userInfo.card) {
        return true;
      } else {
        return false;
      }
    };

    function addUserInfo(step) {
      userInfo = userInfo;
      userInfo[step.stepName] = step;
      currentStep = step.stepName;
      console.log(currentStep);
      $window.localStorage.setItem('app', angular.toJson(userInfo));
    };

    return {
      userInfo: userInfo,
      addUserInfo: addUserInfo,
      currentStep: currentStep,
      isUserWriteData: isUserWriteData,
      checkIsRegComplete: checkIsRegComplete,
      checkIsUserDataStepComplete: checkIsUserDataStepComplete,
      checkIsAddressStepComplete: checkIsAddressStepComplete,
      checkIsCardStepComplete: checkIsCardStepComplete,
    };
  }
}());

(function() {

  'use strict';

  registrationController.$inject = ["registration", "$location", "$scope"];
  angular
    .module('app')
    .component('registration', {
      templateUrl: 'app/pages/registration/registration.html',
      controller: registrationController
    });

  /** @ngInject */
  function registrationController(registration, $location, $scope) {
    var vm = this;

    if (!registration.isUserWriteData) {
      $location.path('/registration/user');
    } else {
      if (registration.checkIsRegComplete()) {
        $location.path('/registration/success');
      }

      if (!registration.checkIsUserDataStepComplete()) {
        $location.path('/registration/user');
      } else if (!registration.checkIsAddressStepComplete()) {
        $location.path('/registration/address');
      } else if (!registration.checkIsCardStepComplete()) {
        $location.path('/registration/card');
      }
    }
  }
}());

